/* Import lib */
import React, { PureComponent } from "react";
import { StaticQuery, graphql } from "gatsby";
import { Layout, Spin, Icon } from 'antd';
/* Import css */
import "./layout.css";
import "./antd.css";
import "./beautiCode.css";
/*declare variable */
// const { Header, Content, Sider } = Layout;
const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;

class IndexPage extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { hidden: true };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ hidden: false });
    }, 700);
  }

  render() {
    const { children } = this.props;
    return (
      this.state.hidden ? <div
        className="center-hello">
        <Spin indicator={antIcon} tip="Tai Pham" />
      </div> :
        <StaticQuery
          query={graphql`
      query {
        site {
          siteMetadata {
            title
          }
        },
        topics: allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date] }) {
          edges {
            node {
              frontmatter {
                categories
                tags
              }
            }
          }
        }
      }
    `}
          render={data => (
            <Layout style={{
              fontSize: 20,
              background: 'rgb(181, 2, 93)'
            }}>
              <div className="s-layout">

                <div className="s-layout__sidebar">
                  <a className="s-sidebar__trigger" href="#0">
                  <Icon style={{color: '#fff'}} type="menu" />
                  </a>

                  <nav className="s-sidebar__nav">
                    <ul>
                      <li>
                        <a className="s-sidebar__nav-link" href="#0">
                          <i className="fa fa-home"></i><em>Home</em>
                        </a>
                      </li>
                      <li>
                        <a className="s-sidebar__nav-link" href="#0">
                          <i className="fa fa-user"></i><em>My Profile</em>
                        </a>
                      </li>
                      <li>
                        <a className="s-sidebar__nav-link" href="#0">
                          <i className="fa fa-camera"></i><em>Camera</em>
                        </a>
                      </li>
                    </ul>
                  </nav>
                </div>

                <Layout className="s-layout__content">
                  {children}
                </Layout>
              </div>
            </Layout>
          )}
        />
    )
  }
}

export default IndexPage;